<template>
  <div class="lotto-generator">
    <!-- button gen -->
    <button @click="generateLottoNumbers" :disabled="!isInputValid || lottoRecords.length >= 30" class="generate-button">(AUTO) Generator</button>
    <button @click="resetLottoNumbers" class="reset-button">Clear</button>
    <!-- box -->
    <div class="box-border">
      <div class="pattern-0">
        <div class="inputs">
          <input
            v-for="(num, index) in userNumbers"
            :key="index"
            type="number"
            v-model.number="userNumbers[index]"
            @input="validateInput(index)"
            :disabled="lottoRecords.length >= 100"
            placeholder="1-49"
          />
          <input
            type="number"
            v-model.number="bonusNumber"
            @input="validateBonusInput"
            :disabled="lottoRecords.length >= 100"
            placeholder="Bonus (1-49)"
          />
        </div>
        <p>Enter Your Favorite Numbers (Optional)</p>
      </div>
    </div>
    <!-- lotto card -->
    <p v-if="lottoRecords.length >= 100">You can enter up to 100 numbers.</p>
    <div v-if="lottoRecords.length > 0" class="numbers">
      <div v-for="(record, index) in lottoRecords" :key="index" :class="['record', `pattern-${index % 5 + 1}`]">
        <div class="record-header">
          <h3>1st Prize Winners</h3>
          <p class="record-date">{{ record.date }}</p>
        </div>
        <div class="record-numbers">
          <span v-for="(number, numIndex) in record.numbers" :key="numIndex" class="number">{{ number }}</span>
          <span class="number bonus-number">{{ record.bonusNumber }}</span>
        </div>
      </div>

      <!-- button bottom -->
      <div class="record-buttons">
        <button @click="printLottoRecords" class="print-button">Print</button>
        <button @click="downloadLottoRecords" class="download-button">Download</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userNumbers: Array(6).fill(null),
      bonusNumber: null,
      lottoRecords: []
    }
  },
  computed: {
    isInputValid () {
      const uniqueNumbers = new Set(this.userNumbers.filter(num => num !== null))
      return (
        uniqueNumbers.size === this.userNumbers.filter(num => num !== null).length &&
        this.userNumbers.every(num => num === null || (num >= 1 && num <= 49)) &&
        (this.bonusNumber === null || (this.bonusNumber >= 1 && this.bonusNumber <= 49))
      )
    }
  },
  methods: {
    validateInput (index) {
      const num = this.userNumbers[index]
      if (num < 1 || num > 49 || this.userNumbers.filter(n => n === num).length > 1) {
        this.userNumbers[index] = null
      }
    },
    validateBonusInput () {
      if (this.bonusNumber < 1 || this.bonusNumber > 49) {
        this.bonusNumber = null
      }
    },
    generateLottoNumbers () {
      if (this.lottoRecords.length < 100 && this.isInputValid) {
        const existingNumbers = this.userNumbers.filter(num => num !== null)
        const numbers = [...existingNumbers]
        while (numbers.length < 6) {
          const randomNumber = Math.floor(Math.random() * 49) + 1
          if (!numbers.includes(randomNumber)) {
            numbers.push(randomNumber)
          }
        }
        const bonusNumber = this.bonusNumber || Math.floor(Math.random() * 49) + 1
        const date = new Date().toLocaleString('en-US')
        this.lottoRecords.unshift({ date, numbers: numbers.sort((a, b) => a - b), bonusNumber })
      }
    },
    resetLottoNumbers () {
      this.lottoRecords = []
      this.userNumbers = Array(6).fill(null)
      this.bonusNumber = null
    },
    printLottoRecords () {
      const printableContent = this.lottoRecords.map(record => `${record.date}: ${record.numbers.join(', ')} Bonus: ${record.bonusNumber}`).join('\n')
      const printWindow = window.open('', '', 'height=400,width=600')
      printWindow.document.write('<pre>' + printableContent + '</pre>')
      printWindow.document.close()
      printWindow.print()
    },
    downloadLottoRecords () {
      const blob = new Blob(
        [this.lottoRecords.map(record => `${record.date}: ${record.numbers.join(', ')} Bonus: ${record.bonusNumber}`).join('\n')],
        { type: 'text/plain;charset=utf-8' }
      )
      const link = document.createElement('a')
      link.href = URL.createObjectURL(blob)
      link.download = 'lotto-records.txt'
      link.click()
      URL.revokeObjectURL(link.href)
    }
  }
}
</script>

<style scoped>
@import '../assets/css/lotto.css';
</style>
